import * as React from 'react';
import Button from '@mui/joy/Button';
import UploadIcon from '@mui/icons-material/Upload';
import { styled } from '@mui/joy';

const VisuallyHiddenInput = styled('input')`
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  left: 0;
  white-space: nowrap;
  width: 1px;
`;

export default function InputFileUpload({ onChange }) {
  return (
    <Button
      component='label'
      role={undefined}
      tabIndex={-1}
      variant='outlined'
      color='neutral'
      startDecorator={<UploadIcon />}>
      Choose a file
      <VisuallyHiddenInput type='file' accept='image/*' onChange={onChange} />
    </Button>
  );
}
